.full-classroom {
  position: relative;
  max-width: calc(1.5*100vh);
  height: 100vh;
}

.image {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  z-index: 0;
  max-width: calc(1.5*100vh);
}

.chat-display-outer {
  position: absolute;
  width: 86%; /* 100 -7 - 7 */
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding: 0 0 40% 0;
  margin: 7% 0 0 7%;
}

.chat-display-inner {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  overflow-y: scroll;
}

.chat-display-scroll-button {
  position: absolute;
  width: 100%;
  bottom: -60px;
  background: transparent;
  color: rgb(43, 83, 130);
  border: none;
  cursor: -webkit-grab;
  cursor: grab;
  -webkit-animation: fadein 1s;
          animation: fadein 1s;
  font-size: 1rem;
}

.chat-bubble-container {
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
}

.chat-right {
  -ms-flex-pack: end;
      justify-content: flex-end;
}

.chat-bubble {
  border-radius: 1rem;
  margin: 0.5rem;
  padding: 0.5rem;
  -webkit-animation: fadein 0.5s;
          animation: fadein 0.5s;
  max-width: 600px;
}

.chat-me {
  background: rgb(26, 132, 245);
  color: white;
}

.chat-them {
  background: rgb(229, 229, 234);
  color: black;
}

.chat-input-container {
  position: absolute;
  bottom: 50px;
  left: 7%;
  right: 7%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: 1;
      flex-direction: 1;
  -ms-flex-pack: center;
      justify-content: center;
  -webkit-animation: fadein 1s;
          animation: fadein 1s;
  -webkit-animation: slidein 1s;
          animation: slidein 1s;
}

@-webkit-keyframes fadein {
    from {
      opacity: 0;
    }
    to {
      opacity: 1;
    }
}

@keyframes fadein {
    from {
      opacity: 0;
    }
    to {
      opacity: 1;
    }
}

@-webkit-keyframes slidein {
  from {
    -webkit-transform: translateX(100%);
            transform: translateX(100%);
  }
  to {
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
}

@keyframes slidein {
  from {
    -webkit-transform: translateX(100%);
            transform: translateX(100%);
  }
  to {
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
}

.chat-input {
  -ms-flex: 1 1;
      flex: 1 1;
  height: 2rem;
}

.chat-mc-button {
  min-height: 2rem;
  min-width: 10rem;
  background-color: rgba(232, 211, 195, 0.8); /* or transparent */
  border: 2px solid rgb(43, 83, 130);
  border-radius: 0.5rem;
  margin: 0 2rem;
  color: rgb(43, 83, 130);
  font-weight: bold;
}

.chat-mc-button:hover {
  color: white;
  background-color: rgb(43, 83, 130);
  cursor: -webkit-grab;
  cursor: grab;
}



